.Contact {
    max-width: 1024px;
    margin: 1em auto;
}
#contact-form {
    display:flex;
    flex-direction: column;
 
}
#contact-form label{
    font-weight: bold;
    margin-top: 1em;

}
#contact-form input{
    padding: 0.5em;
    box-sizing: border-box;

}
#contact-form textarea{
    padding: 0.5em;
    box-sizing: border-box;

}
#contact-form button{
    margin-top: 1em;
    align-self: center;
    width: 150px;
    background-color: black;
    color:white;
    padding:0.5em;
    border: 1px solid black;
    border-radius: 10px;
}